import { Component, AfterViewInit } from '@angular/core';

@Component({
  selector: 'app-nav',
  templateUrl: './nav.component.html',
  styleUrls: ['./nav.component.scss']
})
export class NavComponent implements AfterViewInit {

  constructor() { }

  ngAfterViewInit () {
    this.updateTime();
  }

   updateTime(){
      var d = new Date(),
          days = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'];

      document.querySelector('.navbar__time').innerHTML = this.formatDate(days[d.getDay()], d.getHours(), d.getMinutes());

      //Check every half a second if the time has changed
      var timeout = setTimeout(this.updateTime.bind(this), 500);
  }

  formatDate(day, hour, min){
    var parseHour = this.parseHourMin(hour),
        parseMin = this.parseHourMin(min),
        date = day + ' ' + parseHour + ':' + parseMin;

    return date;
}

parseHourMin(num){
  var parseNum;

  if(num < 10){
      parseNum = '0' + num;
  } else {
      parseNum = num;
  }
  return parseNum;
}


}
