import { Component, AfterViewInit, OnInit } from '@angular/core';
import { fromEvent, Subscription } from 'rxjs';

interface DesktopIcon {
  icon: string;
  iconType: "png" | "svg";
  label: string;
}

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit, AfterViewInit{
  scrollInstances: number =  0;
  scrollIndicator: HTMLElement;

  scrollSubscription: Subscription;

  isLoading: boolean = true;

  ngOnInit() {
   this.handleLoadingScreen();
  }

  ngAfterViewInit(): void {
    this.scrollIndicator = <HTMLElement>document.getElementsByClassName("scrollIndicator")[0];
    const scroll$ = fromEvent(document.querySelector('.parallax'), 'scroll');

    this.scrollSubscription =  scroll$.subscribe(this.handleScroll.bind(this));
  }

  handleLoadingScreen() {
    const loadingAnimationTime = 2;
    const loadingDelay = 0;
    const fadeOutAnimationDuration = .5;

    setTimeout(_=>{
      const loadingScreen = <HTMLElement>document.querySelector("#loading");
      const loadingProgress = <HTMLElement>document.querySelector(".progress");

        this.hideElement(loadingProgress);
        loadingScreen.className += "fade-out";

        setTimeout(_ =>{
          loadingScreen.style.display = "none";
          this.hideElement(loadingScreen);
        }, fadeOutAnimationDuration * 1000);

    }, loadingAnimationTime * 1000 + loadingDelay * 1000)
  }

  handleScroll() {
    this.scrollInstances++;

    if(this.scrollInstances > 15) {
      this.hideElement(this.scrollIndicator);
      this.scrollSubscription.unsubscribe();
    }
  }

  hideElement(element: HTMLElement){
    element.style.display = "none";
  }

  desktopIcons: DesktopIcon[] = [
    {
      icon: "folder",
      iconType: "png",
      label: "Projects"
    },
    {
      icon: "about",
      iconType: "svg",
      label: "About"
    },
    {
      icon: "notchrome",
      iconType: "png",
      label: "Not Chrome"
    },
    {
      icon: "bird",
      iconType: "png",
      label: "Not Flappy Bird"
    },
    {
      icon: "fireworks",
      iconType: "svg",
      label: "Fireworks"
    },
  ];
  
}
